body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #010811;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center; }
  .App__background {
    position: absolute;
    width: 100%;
    height: 100%; }
    .App__background__content {
      border: 1px solid #594208;
      height: 100%;
      width: 80%;
      margin: 0 auto; }
      .App__background__content__header {
        height: 400px;
        border: 2px solid black;
        background-image: -webkit-linear-gradient(355deg, black, transparent, transparent, black);
        background-image: linear-gradient(95deg, black, transparent, transparent, black); }
        .App__background__content__header img {
          margin: 0 auto;
          height: 100%; }

@media only screen and (max-width: 600px) {
  .App__background__content {
    width: 100%;
    border: none; }
    .App__background__content__header {
      width: 100%;
      height: 200px;
      border: none; } }

