.App {
  text-align: center;
  &__background {
    position: absolute;
    width: 100%;
    height: 100%;
    &__content {
      border: 1px solid #594208;
      height: 100%; 
      width: 80%;
      margin: 0 auto;
      &__header {
        height: 400px; 
        border: 2px solid black;
        background-image: linear-gradient(95deg, black, transparent, transparent, black);
        img {
          margin: 0 auto;
          height: 100%;
        }
      }
    }
  }
}


@media only screen and (max-width: 600px) {
  .App {
    &__background {
      &__content {
        width: 100%;
        border: none;
        &__header {
          width: 100%;
          height: 200px; 
          border: none;
        }
      }
    }
  }

}